import React, {useState} from 'react';
// import Modal from '../utils/Modal';
import Popup from './Popup';

function HeroHome() {

  // const [videoModalOpen, setVideoModalOpen] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
 
  const togglePopup = () => {
    setIsOpen(!isOpen);
  } 
   
  return (
    <section className="relative">

      {/* Illustration behind hero content */}
      <div className="absolute left-1/2 transform -translate-x-1/2 bottom-0 pointer-events-none" aria-hidden="true">
        <svg width="1360" height="578" viewBox="0 0 1360 578" xmlns="http://www.w3.org/2000/svg">
          <defs>
            <linearGradient x1="50%" y1="0%" x2="50%" y2="100%" id="illustration-01">
              <stop stopColor="#FFF" offset="0%" />
              <stop stopColor="#EAEAEA" offset="77.402%" />
              <stop stopColor="#DFDFDF" offset="100%" />
            </linearGradient>
          </defs>
          <g fill="url(#illustration-01)" fillRule="evenodd">
            <circle cx="1232" cy="128" r="128" />
            <circle cx="155" cy="443" r="64" />
          </g>
        </svg>
      </div>

      <div className="max-w-6xl mx-auto px-4 sm:px-6">

        {/* Hero content */}
        <div className="pt-32 pb-12 md:pt-40 md:pb-20">

          {/* Section header */}
          <div className="text-center pb-12 md:pb-16">
            <h1 className="text-5xl md:text-6xl font-extrabold leading-tighter tracking-tighter mb-4" data-aos="zoom-y-out">Demo website</h1>
            <div className="max-w-3xl mx-auto">
              <p className="text-xl text-gray-600 mb-8" data-aos="zoom-y-out" data-aos-delay="150">Et has minim elitr intellegat. Mea aeterno eleifend antiopam ad, nam no suscipit quaerendum.</p>
              <p className="text-xl text-gray-600 mb-8" data-aos="zoom-y-out" data-aos-delay="150">At nam minimum ponderum. Est audiam animal molestiae te.</p>
              <div className="max-w-xs mx-auto sm:max-w-none sm:flex sm:justify-center" data-aos="zoom-y-out" data-aos-delay="300">
                <div>
                <div>
    <input type="button" className="btn text-blue bg-white-600 border-blue-700 hover:bg-blue-700 w-full mb-4 sm:w-auto sm:mb-0" value="Know more" onClick={togglePopup}/>
    {isOpen && <Popup
      content={<>
        <input type="text" className="form-input w-full text-gray-800" placeholder="message"></input>
        <input type="text" className="form-input w-full text-gray-800" placeholder="message"></input>
        <input type="text" className="form-input w-full text-gray-800" placeholder="message"></input>
        <button className="btn-sm text-gray-200 bg-blue-900 hover:bg-gray-800 ml-3">Subscribe</button>
      </>}
      handleClose={togglePopup}
    />}
  </div>

                  {/* <a className="btn text-blue bg-white-600 border-blue-700 hover:bg-blue-700 w-full mb-4 sm:w-auto sm:mb-0">Know more</a> */}
                </div>
                <div>
                  <a className="btn text-blue bg-white-900 border-blue-700 hover:bg-blue-800 w-full sm:w-auto sm:ml-4" href="#0">Learn more</a>
                </div>
              </div>
            </div>
          </div>      
        </div> 
      </div> 
    </section>
  );
}

export default HeroHome;